.card {
  border-radius: 2px;
  height: 180px;
  margin: 1rem;
  position: relative;
  width: 325px;
  -webkit-box-shadow: 0 3px 6px #999, 0 3px 6px #999;
          box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: left;
}

.card > .img-container {
  height: 100%;
  overflow: hidden;
  text-align: center;
  background:#6CADDC;
}
.card > .img-container > img {
  width: 100%
}

@media screen and (max-width: 375px) { 
 .img.clickItem{
    width: 33.33% !important;
    height: 10%;
  }
  .card {
    width: 33.33% !important;
    height: 10% !important;
  }
} 


